:root {
  --score-bg-color: #f0f0f0;
}

body {
  background-color: var(--score-bg-color);
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  transition: background-color 1s ease-in-out;
}

#container {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.container {
  display: flex;
}

#content {
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

#loading {
  z-index: 1000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: none;
}

#textContainer {
  background-color: #fff;
  border-radius: 5px;
  width: 100%;
  max-width: 650px;
  max-height: 60vh;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

#categoryContainer {
  width: 100%;
  max-width: 650px;
  margin-bottom: 20px;
}

#categoryBuckets {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.categoryBucket {
  opacity: .3;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  transition: opacity .3s, transform .2s;
  display: flex;
}

.categoryBucket.active {
  opacity: 1;
  transform: scale(1.1);
}

.categoryBucket img {
  object-fit: contain;
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
}

.categoryLabel {
  text-align: center;
  color: #666;
  text-transform: capitalize;
  word-wrap: break-word;
  max-width: 60px;
  font-size: 10px;
  font-weight: bold;
}

#text {
  text-align: center;
  font-size: 20px;
  line-height: 1.5;
}

#relatedItems {
  color: #666;
  text-align: center;
  max-width: 100%;
  margin-bottom: 10px;
  font-size: 12px;
}

#score-container {
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 10px;
  padding: 5px 0;
}

#sourceContainer {
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

#titlerow, #otherRows {
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

.relatedItems {
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  background-color: #f0f0f0;
  border-radius: 5px;
  width: 48%;
  padding: 5px;
  overflow: hidden;
}

#relatedAuthor {
  font-size: 14px;
  font-weight: bold;
}

#relatedTitle {
  font-size: 14px;
  font-style: italic;
  font-weight: bold;
}

#relatedId, #relatedScore {
  font-size: 12px;
  font-style: italic;
}

.sidebar {
  background-color: #f6f1a5;
  flex-direction: column;
  width: 200px;
  height: 100vh;
  padding: 10px;
  display: flex;
}

#toggleSidebar {
  align-self: flex-start;
}

.score-box {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  flex: none;
  align-items: center;
  min-width: 100px;
  margin: 2px;
  padding: 5px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.score-row {
  flex-wrap: nowrap;
  justify-content: space-around;
  display: flex;
  overflow-x: auto;
}

.score-column {
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow-y: auto;
}

.score-label {
  margin-bottom: 5px;
  font-size: .9em;
  font-weight: bold;
}

.score-value {
  font-size: .9em;
}

.hidden {
  display: none !important;
}

.highlightSafe {
  white-space: pre-wrap;
  background: linear-gradient(100deg, #b8ffaf00 1%, #b8ffaf 2.5%, #b8ffaf80 5.7%, #b8ffaf1a 93%, #b8ffafb4 95%, #b8ffaf00 98%), linear-gradient(182deg, #b8ffaf00, #b8ffaf4d 8%, #b8ffaf00 15%);
  display: inline-block;
}

::selection {
  color: #000;
  background: #b8ffaf;
}

#scatterplot svg {
  width: 100%;
  height: 100%;
}

.point {
  fill: #c0c0f0;
  opacity: 0;
}

.point.highlighted {
  fill: pink;
  stroke: #000;
  opacity: .8;
}

.point.latest {
  fill: #ffbf00;
  stroke: #000;
  opacity: 1;
}

.point-label {
  opacity: .8;
  text-anchor: middle;
  fill: #2d1f04;
  font-size: 10px;
}

.connection-line {
  stroke: #000;
  stroke-width: 1px;
  stroke-dasharray: 4 4;
  opacity: .5;
  fill: none;
}

.cluster-icon {
  pointer-events: none;
  z-index: 10;
  filter: drop-shadow(0 0 4px #fffc);
  background: radial-gradient(circle, #ffffffe6 0%, #ffffffb3 30%, #ffffff4d 60%, #fff0 100%);
  border-radius: 50%;
  width: 35px;
  height: 35px;
}

.tooltip {
  z-index: 1000;
  pointer-events: none;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 3px;
  padding: 5px;
  position: absolute;
}

button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  margin-bottom: 10px;
  padding: 10px;
}

.alegreya-sc-regular {
  font-family: Alegreya SC, serif;
