  font-weight: 800;
}

.alegreya-sc-black {
  font-family: Alegreya SC, serif;
  font-style: normal;
  font-weight: 900;
}

.alegreya-sc-regular-italic {
  font-family: Alegreya SC, serif;
  font-style: italic;
  font-weight: 400;
}

.alegreya-sc-medium-italic {
  font-family: Alegreya SC, serif;
  font-style: italic;
  font-weight: 500;
}

.alegreya-sc-bold-italic {
  font-family: Alegreya SC, serif;
  font-style: italic;
  font-weight: 700;
}

.alegreya-sc-extrabold-italic {
  font-family: Alegreya SC, serif;
