  font-weight: 800;
}

.alegreya-sc-black-italic {
  font-family: Alegreya SC, serif;
  font-style: italic;
  font-weight: 900;
}

.icon-labels-group text {
  filter: drop-shadow(0 0 4px #fffc);
}

.icon-labels-group text:before {
  content: "";
  z-index: -1;
  background: radial-gradient(circle, #ffffffe6 0%, #ffffffb3 50%, #ffffff4d 70%, #fff0 100%);
  width: 120%;
  height: 120%;
  position: absolute;
}
